#list-header {
	background: #2980b9;
	margin: 0;
	color: white;
	padding: 10px 20px;
	text-transform: uppercase;
	font-size: 24px;
}

.fa-plus {
	float: right;
}

.tasks-body {
	/* font-family: Roboto; */

	background: #2980b9; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to right,
		#ffffff,
		#6dd5fa,
		#2980b9
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to right,
		#ffffff,
		#6dd5fa,
		#2980b9
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

#tasks-container {
	width: 360px;
	align-content: center;
	margin: 100px auto;
	background-color: #f7f7f7;
	box-shadow: 0 0 3px rgba(0, 0, 0, 0.1);
}

input {
	font-size: 18px;
	color: #2980b9;
	background-color: #f7f7f7;
	width: 100%;
	padding: 13px 13px 13px 20px;
	box-sizing: border-box;
	border: 3px solid rgba(0, 0, 0, 0);
}

input:focus {
	background: #fff;
	border: 3px solid #2980b9;
	outline: none;
}

li {
	background: #fff;
	line-height: 40px;
	color: #666;
}

li:nth-child(2n) {
	background: #f7f7f7;
}

li:hover span {
	width: 40px;
	opacity: 1;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

::placeholder {
	color: lightgray;
}

.tasks-span {
	background: #e74c3c;
	height: 40px;
	width: 0px;
	margin-right: 20px;
	text-align: center;
	color: white;
	display: inline-block;
	transition: 0.2s linear;
	opacity: 0;
}

.ReactCollapse--collapse {
	transition: height 0.3s;
}

.tasks-div {
	transition: 5s;
}

